@main-color: #283645;
@second-color: #d22856;

.font(@font-weight, @font-size, @line-height, @color) {
  font-weight: @font-weight;
  font-size: @font-size;
  line-height: @line-height;
  font-family: "Opens Sans", sans-serif;
  color: @color;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
